import JustValidate from 'just-validate';

const form = document.querySelector('.form');
const telSelector = form.querySelector('input[type="tel"]');
const inputMask = new Inputmask('+7 (999) 999-99-99');
inputMask.mask(telSelector);

const validation = new JustValidate('.form', {
	errorFieldCssClass: "error",
	errorLabelCssClass: "error",
	successFieldCssClass: "success",
	focusInvalidField: false,
	lockForm: false,
});

validation
	.addField('#name', [
		{
			rule: 'minLength',
			value: 2,
			errorMessage: 'Имя должно содержать более 2 символов'
		},
		{
			rule: 'maxLength',
			value: 30,
		},
		{
			rule: 'required',
			value: true,
			errorMessage: 'Введите имя!'
		}
	])
	.addField('#telegram', [
		{
			rule: 'minLength',
			value: 2,
			errorMessage: 'Ник либо id не может содержать менее 2 символов'
		},
		{
			rule: 'maxLength',
			value: 30,
		},
		{
			rule: 'required',
			value: true,
			errorMessage: 'Введите ник либо id вашего telegram!'
		}
	])
	.addField('#email', [
		{
			rule: 'required',
			value: true,
			errorMessage: 'Email обязателен',
		},
		{
			rule: 'email',
			value: true,
			errorMessage: 'Введите корректный Email',
		},
	])
	.addField('#tel', [
		{
			rule: 'required',
			value: true,
			errorMessage: 'Телефон обязателен',
		},
		{
			rule: 'function',
			validator: function () {
				const phone = telSelector.inputmask.unmaskedvalue();
				return phone.length === 10;
			},
			errorMessage: 'Введите корректный телефон',
		},
	]).onSuccess((event) => {
	let formData = new FormData(event.target);
	let xhr = new XMLHttpRequest();
	xhr.onreadystatechange = function () {
		if (xhr.readyState === 4) {
			if (xhr.status === 200) {
				console.log('Отправлено');
			}
		}
	}
	xhr.open('POST', 'mail.php', true);
	xhr.send(formData);
	event.target.reset();
	document.querySelectorAll('.form__input').forEach(e => {
		e.classList.remove('success')
	})
});
